import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"
import ResourcesIcon from "../components/icons/resourcesIcon"
import {
  BehanceIcon,
  CssIcon,
  HtmlIcon,
  JavaScriptIcon,
  GitIcon,
  DribbbleIcon,
  BookIcon,
} from "../components/icons/logoIcons"

const Resources = () => (
  <Layout>
    <SEO title="Resources" />
    <div className="resources">
      <div className="container">
        <div className="container__content">
          <div>
            <h1>Resources</h1>
            <h3>The one stop shop for all your development needs.</h3>
          </div>
          <ResourcesIcon />
        </div>
      </div>

      <div className="container">
        <div className="container__inner">
          <div className="margin-bottom-medium">
            <h3 className="resources__header margin-bottom-medium">
              Design Inspiration
            </h3>
            <div className="resources__row">
              <a
                href="https://www.behance.net/galleries"
                className="resources__row--item"
              >
                <BehanceIcon />
                <p>Behance</p>
              </a>
              <a href="https://dribbble.com" className="resources__row--item">
                <DribbbleIcon />
                <p>Dribbble</p>
              </a>
            </div>
          </div>

          <div className="margin-bottom-medium">
            <h3 className="resources__header margin-bottom-medium">
              Newsletters
            </h3>
            <div className="resources__row">
              <a
                href="https://frontendfoc.us/"
                className="resources__row--item"
              >
                <HtmlIcon />
                <p>FrontEnd Focus</p>
              </a>
              <a
                href="https://css-weekly.com/"
                className="resources__row--item"
              >
                <CssIcon />
                <p>CSS Weekly</p>
              </a>
              <a
                href="https://javascriptweekly.com/"
                className="resources__row--item"
              >
                <JavaScriptIcon />
                <p>JavaScript Weekly</p>
              </a>
            </div>
          </div>

          <div className="margin-bottom-medium">
            <h3 className="resources__header margin-bottom-medium">
              Misc Resources
            </h3>
            <div className="resources__row">
              <a
                href="https://github.com/bennadel/git-cheat-sheet"
                className="resources__row--item"
              >
                <GitIcon />
                <p>Git Cheat Sheet</p>
              </a>
              <a
                href="https://javascript.info/"
                className="resources__row--item"
              >
                <JavaScriptIcon />
                <p>JavaScript Info</p>
              </a>
              <a
                href="https://free-for.dev/#/"
                className="resources__row--item"
              >
                <BookIcon />
                <p>Free For Dev</p>
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Layout>
)

export default Resources
